import React from 'react'
import { connect } from 'react-redux'

const ResultPageComponent = ({errors}) => {

    return (
        
        <div className="result"> 
            <h1>{errors} {errors === 1 ? "error": "errors" }</h1>
        </div>
    )
}

export const ResultPage = connect(
    (state) => ({
        errors: state.body.errors
    }),
    null
)(ResultPageComponent)